
@import "colors.scss";
$screen-sm-min:768px;
$screen-md-min:992px;
$screen-lg-min:1200px;

$header:#333;
body{
    background: $base;
}
.icon-gray{
    color: $icon-gray;
}
.icon-white{
    color: $icon-white;
}
.buttom-blue{
    color: $buttom-blue;
}
/* Small devices (tablets, 768px and up) */
@media (min-width: $screen-sm-min) {

.navbar{
    .navbar-top {       
       .navbar-header{
        display: inline-block;
       }
       .search-block{
        @extend .navbar-header;
       } 
       .list-bussines-block {
        @extend .navbar-header;
       
       }
       .main-logo-block{
         @extend .navbar-header;
          a{
            display: inline-block;
            font-size: 2em;
            text-decoration: none;
          }
          padding: 0 30px;
       }  
         padding: 20px 0 0 0;
    }
    .top-menu{
        padding: 10px 0 0 0;
    }
}

#form-search{
    input {
      border-radius: 0;
    }
    button{
        @extend input
    }   

}
 #search-where{
        width: 100px;
    }
}

/* Medium devices (desktops, 992px and up) */
@media (min-width: $screen-md-min) {
  
    #pro-search{
        width:280px;
    }
   

}

/* Large devices (large desktops, 1200px and up) */
@media (min-width: $screen-lg-min) {
    
    #pro-search{
        width:400px;
    }
    

}